import React, {ChangeEvent, useState} from 'react';
import styled from "styled-components";
import {Button, TextField} from "@mui/material";
import {Simulate} from "react-dom/test-utils";
import error = Simulate.error;


type propsType = {
    addNewItem: (title: string) => void

}
export const InputForm = (props: propsType) => {
    const [titleInput, setInputTitle] = useState('')
    const [errorInput, setErrorInput] = useState<string>('')
    const trimmedValue = titleInput.trim()
    const inputHandler = (event: ChangeEvent<HTMLInputElement>) => {
        setInputTitle(event.currentTarget.value)
        setErrorInput('')
    }

    const submitHandler = () => {
        if (trimmedValue) {
            props.addNewItem(trimmedValue)
        } else {
            setErrorInput('field is required')
        }
        setInputTitle('')
    }

    const onPressKey = (e: React.KeyboardEvent<HTMLDivElement>) => {

        if (e.ctrlKey && e.code === 'Enter' && trimmedValue) {
            submitHandler()
        }
    }
    return (<>
        <Wrapper>
            <TextField
                error={!!errorInput}
                helperText={errorInput && "empty name"}
                label="new task" variant={"outlined"}
                onChange={inputHandler}
                value={titleInput}
                onKeyDown={(e) => onPressKey(e)}
            />



            <Button onClick={() => submitHandler()} variant={"contained"} color={'primary'}>+</Button>
        </Wrapper>
            <div>press ctrl+Enter to add</div>
        </>
    );
}


const Wrapper = styled.div`

  & > button {
    margin-left: 10px;

    height: 55px;
  }
`

