import React from "react";
import styled from "styled-components";
import {NavLink, Outlet} from "react-router-dom";

export const NavMenu = () => {
    return (<>
            <Projects>


                <StyledNavLink to="/useparamsapp">UseParamsApp</StyledNavLink>
                <StyledNavLink to="/sqlconnect">SqlConnect</StyledNavLink>
                <StyledNavLink to="/counterapp">CounterApp</StyledNavLink>
                <StyledNavLink to="/todolistapp">TodolistApp</StyledNavLink>
                <StyledNavLink to="/queueapp">QueueApp</StyledNavLink>
                <StyledNavLink to="/rating">Rating</StyledNavLink>
                <StyledNavLink to="/onoff">OnOff</StyledNavLink>

            </Projects>
            <Outlet/>
        </>
    )
}
const Projects = styled.div`

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 30px;
  justify-content: space-between;

`


const StyledNavLink = styled(NavLink)`
  text-decoration: none;
  padding: 1rem;
  border: lightgreen solid;
  margin: 10px;


  &:hover {
    background-color: lightgreen;
  }

  &.active {
    background-color: lightgreen;
    border-radius: 20px;
  }

  &.active:hover {

    border-radius: 20px;
  }
`