import {Todolist} from "./Todolist";
import React, {useState} from 'react';
import {v1} from "uuid";
import styled from "styled-components";
import {TypeData} from "./TodolistApp";

export type TaskType = {

    id: string;
    title: string;
    isDone: boolean;
};
export type PropsType = {

    title: string
    onRemoveTodolist:()=>void
    number:number
}

function TodolistComponent(props: PropsType) {


    let [tasks, setTasks] = useState<TaskType[]>([]);

    const addNewItem = (title: string) => {
        setTasks([...tasks, {id: v1(), title, isDone: false}])
    }

    function removeTask(id: string) {
        let filteredTasks = tasks.filter((item: { id: string; }) => item.id !== id);
        setTasks(filteredTasks);
    }

    const toggleTaskCheckbox = (id: string) => {
        setTasks(tasks.map((item:TaskType) => item.id === id ? {...item, isDone:!item.isDone} : item))
    }

const onChangeTitle= (id:string, newValue:string)=>{
    setTasks(tasks.map((item: TaskType) => item.id === id ? {...item, title:newValue} : item))

    }


    let [filter, setFilter] = useState("all");

    function changeFilter(value: string) {
        setFilter(value);
    }

    if (filter === "active") {
        tasks = tasks.filter((item:TaskType) => item.isDone === false);
    }
    if (filter === "completed") {
        tasks = tasks.filter((item: TaskType) => item.isDone === true);
    }
//filtration

    const reset = () => {
        setTasks([])
        setFilter('all')
    }


    return (
<Wrapper>

        <Todolist     title={props.title}
                      tasks={tasks}
                      removeTask={removeTask}
                      changeFilter={changeFilter}
                      addNewItem={addNewItem}
                      reset={reset}
                      toggleTaskCheckbox={toggleTaskCheckbox}
                      filter={filter}
                      onRemoveTodolist={props.onRemoveTodolist}
                      number={props.number}
                      onChangeTitle={onChangeTitle}


            />

</Wrapper>
    );
}

export default TodolistComponent;


const Wrapper = styled.div`
    margin: 5px;
  border: green solid;
 
    `