import TodolistComponent from "./TodolistComponent";
import {v1} from "uuid";
import styled from "styled-components";
import React, {ChangeEvent, useState} from "react";
import {Button, TextField} from "@mui/material";

export type TypeData = {
    id: string;
    number: any;
    name: string;
}

function TodolistApp() {
    const [todoLists, setTodoLists] = useState<TypeData[]>([])
    const [nameList, setNamelist] = useState<string>('')
    const [errorInput, setErrorInput] = useState<boolean>(false)


    const nameTodolistInput = (event: ChangeEvent<HTMLInputElement>) => {
        setNamelist(event.target.value)
    }
    const addTodolist = () => {
        if (nameList.trim()) {
            setTodoLists([...todoLists, {
                id: v1(),
                number: todoLists.length > 0 ? todoLists[todoLists.length - 1].number + 1 : 1,
                name: nameList
            }])
            setNamelist('')
        } else {
            setErrorInput(true)
        }
    }

    if (nameList.trim().length > 0 && errorInput) {
        setErrorInput(false)
    }
    const onRemoveTodolist = (id: string) => {
        setTodoLists(todoLists.filter((item: TypeData) => item.id !== id))
    }


    return (
        <Wrapper>
            <Item><h3> This component are Todolists</h3>

                <TextField
                    error={errorInput}
                    helperText={errorInput && "empty name"}
                    label="new task" variant={"outlined"}
                    onChange={nameTodolistInput}
                    value={nameList}

                />
                <Button variant={"outlined"} onClick={addTodolist}>add new list</Button>
            </Item>
            <TodoStyle>
                {todoLists.map((item: TypeData) => <TodolistComponent title={item.name} key={item.id}
                                                                      onRemoveTodolist={() => onRemoveTodolist(item.id)}
                                                                      number={item.number}/>)}
            </TodoStyle>
        </Wrapper>

    );
}

export default TodolistApp;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;



`
const Item = styled.div`
  display: flex;
  flex-direction: column;
  margin: 5px;
`

const TodoStyle = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`